                    <div class="box-front">
                        <img :src="box.image" alt="box image" />
                    </div>
                    <div class="box-back">
                        <p>{{ box.description }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// 引入 boxList.js 中的 boxes 数据
import { boxes } from '@/constants/boxList.js';

export default {
    name: "BoxListComponent",
    data() {
        return {
            boxes, // 使用引入的 boxes 数据
        };
    },
};
