<style scoped>
.container {
    width: 100%;
    background: linear-gradient(to bottom, #005b99 50%, transparent 50%);
    padding: 20px;
    margin-bottom: 20px;
}

.box-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 80px;
    justify-content: center;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}

.box {
    position: relative;
    text-align: center;
    perspective: 1000px;
    background-color: white;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
}

.box:hover {
    transform: translateY(-10px);
