<template>
  <div class="homepage">
    <header class="header">
      <!-- 头部内容保留不变 -->
      <nav class="nav-menu">
        <a href="/" class="nav-item">HOME</a>
        <a href="/news" class="nav-item">NEWS</a>
        <a href="/people" class="nav-item">PEOPLE</a>
        <a href="/publications" class="nav-item">PUBLICATIONS</a>
        <a href="/teaching" class="nav-item">TEACHING</a>
        <a href="/hacking" class="nav-item">HACKING</a>
        <a href="/open-source" class="nav-item">OPEN-SOURCE PROJECTS</a>
        <a href="/hiring" class="nav-item">HIRING</a>
      </nav>

      <div class="header-title">
        <h1 class="small-title">CSE</h1>
        <h2 class="main-title">The Hongkong University of Science and Technology</h2>
        <h3 class="sub-title">Cybersecurity Lab</h3>
      </div>

      <div class="scroll-down-btn" @click="scrollToIntroduction">
        <svg class="scroll-icon" width="48" height="48" viewBox="0 0 24 24" fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path d="M12 16l-6-6 1.41-1.41L12 13.17l4.59-4.58L18 10z" fill="#00FF7F" />
        </svg>
      </div>
    </header>

    <!-- 介绍内容 -->
    <div id="introduction-content" :class="{ animated: isAnimationTriggered }">
      <Introduction :isAnimated="isAnimationTriggered" />

      <!-- 滚动到成员按钮 -->
      <div class="scroll-down-btn-intro" @click="scrollToTeam">
        <svg class="scroll-icon" width="48" height="48" viewBox="0 0 24 24" fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path d="M12 16l-6-6 1.41-1.41L12 13.17l4.59-4.58L18 10z" fill="#00FF7F" />
        </svg>
      </div>
    </div>

    <!-- 成员介绍 -->
    <div id="carousel-content">
      <CarouselComponent :cards="cards" />

      <!-- 滚动到BoxList按钮 -->
      <div class="scroll-down-btn-intro" @click="scrollToBoxList">
        <svg class="scroll-icon" width="48" height="48" viewBox="0 0 24 24" fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path d="M12 16l-6-6 1.41-1.41L12 13.17l4.59-4.58L18 10z" fill="#00FF7F" />
        </svg>
      </div>

    </div>

    <!-- 分割线 -->
    <el-divider class="custom-divider">
      <el-icon>
        <StarFilled />
      </el-icon>
    </el-divider>

    <!-- BoxList -->
    <div id="boxList-content">
      <BoxListComponent />
    </div>

    <!-- News -->
    <div id="news-content">
      <NewsComponent />
    </div>


    <FooterComponent />

  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { personData } from '@/constants/person.js';
// 引入新组件
import Introduction from '@/components/home/IntroductionComponent.vue';
import CarouselComponent from '@/components/home/CarouselComponent.vue';
import BoxListComponent from '@/components/home/BoxListComponent.vue';
import NewsComponent from '@/components/home/NewsComponents.vue';
import FooterComponent from '@/components/footer/FooterComponent.vue'


import { StarFilled } from '@element-plus/icons-vue';

export default {
  name: 'HomePage',
  components: {
    // 注册组件
    Introduction,
    CarouselComponent,
    BoxListComponent,
    NewsComponent,
    FooterComponent,

    StarFilled,
  },
  setup() {
    const isAnimationTriggered = ref(false); // 控制动画触发

    onMounted(() => {
      const introductionContent = document.getElementById('introduction-content');

      const observer = new IntersectionObserver((entries) => {
        if (entries[0].isIntersecting) {
          isAnimationTriggered.value = true;
          observer.unobserve(introductionContent);
        }
      }, {
        threshold: 0.1,
      });

      observer.observe(introductionContent);
    });

    return {
      isAnimationTriggered,
      StarFilled,
    };
  },
  data() {
    return {
      cards: personData, // 使用导入的数据
    };
  },
  methods: {
    scrollToIntroduction() {
      const mainContent = document.getElementById('introduction-content');
      mainContent.scrollIntoView({ behavior: 'smooth' });
    },
    scrollToTeam() {
      const mainContent = document.getElementById('carousel-content');
      mainContent.scrollIntoView({ behavior: 'smooth' });
    },
    scrollToBoxList() {
      const mainContent = document.getElementById('boxList-content');
      mainContent.scrollIntoView({ behavior: 'smooth' });
    },
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* 头部样式 */
.header {
  background-image:
    linear-gradient(rgba(9, 19, 68, 0.9), rgba(32, 66, 146, 0.7)),
    url('@/assets/homeBg.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  user-select: none;
  height: 600px;
}

.nav-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 90%;
  max-width: 1200px;
  position: relative;
  z-index: 2;
}

.nav-item {
  color: #ffffff;
  text-decoration: none;
  font-size: 14px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s;
  flex-grow: 1;
  text-align: center;
  padding: 0 15px;
  cursor: pointer;
}

.nav-item:hover,
.nav-item.active {
  background-color: rgba(255, 255, 255, 0.1);
}

.header-title {
  text-align: center;
  color: #ffffff;
  z-index: 2;
  margin-top: 100px;
}

.small-title {
  font-size: 36px;
  font-weight: bold;
  color: #00FF7F;
  margin-bottom: 20px;
}

.main-title {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
}

.sub-title {
  font-size: 24px;
  font-weight: normal;
}

.scroll-down-btn {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  z-index: 3;
  animation: bounce 2s infinite;
}

.scroll-down-btn-intro {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  animation: bounce 2s infinite;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes bounce {

  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateX(-50%) translateY(0);
  }

  40% {
    transform: translateX(-50%) translateY(-10px);
  }

  60% {
    transform: translateX(-50%) translateY(-5px);
  }
}

#introduction-content {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

#introduction-content.animated {
  opacity: 1;
  transform: translateY(0);
}

/* 自定义分割线样式 */
.custom-divider {
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  /* 控制分割线的宽度 */
  margin: 20px auto;
  /* 居中对齐 */
}

.custom-divider .el-icon {
  color: #00FF7F;
}

.custom-divider::before,
.custom-divider::after {
  border-color: #00FF7F;
  width: 20%;
  /* 控制两侧线条的长度 */
}
</style>