import { createRouter, createWebHistory } from 'vue-router'
import HomePage from '../views/HomePage.vue'
import DefaultLayout from '../layouts/DefaultLayout.vue'

import HackingView from '@/views/HackingView.vue'
import HiringView from '@/views/HiringView.vue'
import NewsView from '@/views/NewsView.vue'
import PeopleView from '@/views/PeopleView.vue'
import PublicationsView from '@/views/PublicationsView.vue'
import TeachingView from '@/views/TeachingView.vue'
import OpenSourceProjectsView from '@/views/OpenSourceProjectsView.vue'

const routes = [
    {
        path: '/',
        name: 'HomePage',
        component: HomePage
    },
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '/news',
                name: 'NewsView',
                component: NewsView
            }
        ]
    },
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '/people',
                name: 'PeopleView',
                component: PeopleView
            }
        ]
    },
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '/publications',
                name: 'PublicationsView',
                component: PublicationsView
            }
        ]
    },
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '/teaching',
                name: 'TeachingView',
                component: TeachingView
            }
        ]
    },
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '/hacking',
                name: 'HackingView',
                component: HackingView
            }
        ]
    },
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '/hiring',
                name: 'HiringView',
                component: HiringView
            }
        ]
    },
    {
        path: '/',
        component: DefaultLayout,
        children: [
            {
                path: '/open-source',
                name: 'OpenSourceProjectsView',
                component: OpenSourceProjectsView
            }
        ]
    }
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

export default router