<template>
  <div id="card-content" class="carousel-section">
    <!-- 新增标题 -->
    <el-divider content-position="left">
      <h2 class="carousel-title">Research Team</h2>
    </el-divider>

    <div class="carousel-container">
      <!-- 左按钮 -->
      <button @click="prevSlide" @mouseenter="stopAutoPlay" @mouseleave="startAutoPlay" class="carousel-btn left-btn">
        <svg class="carousel-icon" width="48" height="48" viewBox="0 0 24 24" fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path d="M14 18l-6-6 6-6 1.41 1.41L10.83 12l4.58 4.59L14 18z" fill="#00FF7F" />
        </svg>
      </button>

      <!-- 卡片容器 -->
      <div class="carousel-wrapper">
        <div class="carousel" :style="{ height: currentCardHeight + 'px' }">
          <!-- 动态渲染卡片 -->
          <div v-for="(card, index) in cards" :key="index" :class="['card', getCardClass(index)]"
            :style="getCardStyle(index)" @mouseenter="handleMouseEnter(index)" @mouseleave="handleMouseLeave(index)">
            <div class="card-image-wrapper">
              <img :src="card.image" alt="图片" class="card-image" />
            </div>
            <div class="card-content">
              <h3 class="card-title">{{ card.title }}</h3>
              <h4 class="card-subtitle">{{ card.subtitle }}</h4>
              <p class="card-text">{{ card.text }}</p>
              <!-- 在按钮上添加点击事件，点击后跳转到 /people -->
              <el-button type="success" class="card-button" @click="navigateToPeople">{{ card.buttonText }}</el-button>
            </div>
          </div>
        </div>
      </div>

      <!-- 右按钮 -->
      <button @click="nextSlide" @mouseenter="stopAutoPlay" @mouseleave="startAutoPlay" class="carousel-btn right-btn">
        <svg class="carousel-icon" width="48" height="48" viewBox="0 0 24 24" fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path d="M10 6l6 6-6 6-1.41-1.41L13.17 12l-4.58-4.59L10 6z" fill="#00FF7F" />
        </svg>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "CarouselComponent",
  props: {
    cards: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      currentIndex: 0, // 当前中间卡片的索引
      autoPlayInterval: null, // 定时器引用
      isHovered: false, // 是否悬停在中间卡片上
      currentCardHeight: 0, // 当前显示卡片的高度
    };
  },
  computed: {
    totalCards() {
      return this.cards.length;
    },
  },
  methods: {
    nextSlide() {
      if (!this.isHovered) {
        this.currentIndex = (this.currentIndex + 1) % this.totalCards;
        this.updateCardHeight();
      }
    },
    prevSlide() {
      if (!this.isHovered) {
        this.currentIndex = (this.currentIndex - 1 + this.totalCards) % this.totalCards;
        this.updateCardHeight();
      }
    },
    getCardClass(index) {
      if (index === this.currentIndex) return "active"; // 中间的卡片
      if (index === (this.currentIndex - 1 + this.totalCards) % this.totalCards) return "left"; // 左边的卡片
      if (index === (this.currentIndex + 1) % this.totalCards) return "right"; // 右边的卡片
      return "";
    },
    getCardStyle(index) {
      const distance = 60; // 卡片间距
      const scale = 0.8; // 左右卡片缩放比例
      const rotateY = 30; // 左右卡片旋转角度
      const hoveredScale = 1.1; // 中间卡片悬停时的放大比例

      if (index === this.currentIndex) {
        return {
          transform: `translateX(0) scale(${this.isHovered ? hoveredScale : 1}) rotateY(0deg)`,
          zIndex: 2,
          opacity: 1,
          transition: "transform 0.3s ease-in-out",
        };
      } else if (index === (this.currentIndex - 1 + this.totalCards) % this.totalCards) {
        return {
          transform: `translateX(-${distance}%) scale(${scale}) rotateY(${rotateY}deg)`,
          zIndex: 1,
          opacity: 0.7,
        };
      } else if (index === (this.currentIndex + 1) % this.totalCards) {
        return {
          transform: `translateX(${distance}%) scale(${scale}) rotateY(-${rotateY}deg)`,
          zIndex: 1,
          opacity: 0.7,
        };
      } else {
        return {
          transform: `scale(0.5)`,
          opacity: 0,
          zIndex: 0,
        };
      }
    },
    startAutoPlay() {
      this.autoPlayInterval = setInterval(() => {
        this.nextSlide();
      }, 3000); // 每3秒切换一次
    },
    stopAutoPlay() {
      clearInterval(this.autoPlayInterval);
      this.autoPlayInterval = null;
    },
    handleMouseEnter(index) {
      if (index === this.currentIndex) {
        this.isHovered = true;
        this.stopAutoPlay();
      }
    },
    handleMouseLeave(index) {
      if (index === this.currentIndex) {
        this.isHovered = false;
        this.startAutoPlay();
      }
    },
    updateCardHeight() {
      this.$nextTick(() => {
        const activeCard = this.$el.querySelector(".card.active");
        if (activeCard) {
          this.currentCardHeight = activeCard.offsetHeight;
        }
      });
    },
    navigateToPeople() {
      this.$router.push('/people');
    }
  },
  mounted() {
    this.updateCardHeight();
    this.startAutoPlay();
  },
  beforeUnmount() {
    this.stopAutoPlay();
  },
};
</script>

<style scoped>
.carousel-section {
  /* 浅色渐变背景 */
  padding: 30px;
}

.carousel-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #1f78b4;
  margin-bottom: 20px;
  user-select: none;
}

.carousel-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  max-width: 100%;
  margin: 100px auto;
  overflow: hidden;
  padding: 0 80px;
  perspective: 1000px;
}

.carousel-wrapper {
  overflow: hidden;
  width: 100%;
  position: relative;
}

.carousel {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.5s ease-in-out, height 0.5s ease;
}

/* 卡片样式 */
.card {
  display: flex;
  align-items: center;
  position: absolute;
  padding: 20px;
  border-radius: 12px;
  overflow: visible;
  background: white;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.1);
  width: 60%;
  height: auto;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out, z-index 0.5s;
}

.card-image-wrapper {
  position: absolute;
  top: 15%;
  left: -60px;
  width: 260px;
  height: 260px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  user-select: none;
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 220px;
  min-height: 300px;
  user-select: none;
}

.card-title {
  font-size: 24px;
  color: #007bff;
  margin-bottom: 10px;
  font-weight: bold;
}

.card-subtitle {
  font-size: 20px;
  color: #333;
  margin-bottom: 15px;
  font-weight: bold;
}

.card-text {
  font-size: 16px;
  color: #666;
  margin-bottom: 20px;
  flex-grow: 1;
}

.card-button {
  align-self: flex-end;
  margin-top: auto;
  background-color: #22c55e;
  color: #fff;
  border-radius: 20px;
  padding: 10px 20px;
  margin-bottom: 20px;
  margin-right: 20px;
  position: relative;
}

.card-button:hover {
  background-color: #16a34a;
  color: #fff;
}

.carousel .active {
  z-index: 2;
}

.carousel .left,
.carousel .right {
  z-index: 1;
}

/* 轮播按钮 */
.carousel-btn {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

.left-btn {
  left: 20px;
}

.right-btn {
  right: 20px;
}

/* 为按钮图标添加放大效果 */
.carousel-icon {
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.carousel-btn:hover .carousel-icon {
  transform: scale(1.5);
  opacity: 0.7;
}
</style>