<template>
    <div class="news-component">
        <div class="news-header">
            <p class="news-title">News</p>
            <div class="underline"></div>
        </div>
        <el-carousel :autoplay="false" :interval="0" arrow="always" class="news-carousel">
            <el-carousel-item v-for="(news, index) in chunkedNewsList" :key="index">
                <div class="news-group">
                    <div class="news-item" v-for="item in news" :key="item.title">
                        <a :href="item.url" target="_blank" class="news-link">
                            <p class="news-content">
                                <strong>{{ item.title }}:</strong> {{ item.description }}
                            </p>
                        </a>
                    </div>
                </div>
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
// 从外部文件导入 newsList
import { newsList } from "@/constants/newsList.js";

export default {
    name: "NewsComponent",
    data() {
        return {
            newsList // 使用导入的 newsList 数据
        };
    },
    computed: {
        chunkedNewsList() {
            const chunkSize = 2;
            const chunks = [];
            for (let i = 0; i < this.newsList.length; i += chunkSize) {
                chunks.push(this.newsList.slice(i, i + chunkSize));
            }
            return chunks;
        }
    }
};
</script>

<style scoped>
.news-component {
    text-align: center;
    padding: 20px;
    margin: 50px 0;
}

.news-header {
    margin-bottom: 20px;
}

.news-title {
    color: #1f78b4;
    font-weight: bold;
    margin: 0;
    font-size: 24px;
}

.underline {
    width: 60px;
    height: 4px;
    background-color: #239b56;
    margin: 10px auto;
}

.news-carousel {
    width: 100%;
    margin: 0 auto;
    position: relative;
}

.news-group {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.news-item {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48%;
    height: 100%;
}

.news-link {
    text-decoration: none;
    /* 移除链接下划线 */
}

.news-link:hover .news-content {
    text-decoration: underline;
    /* 鼠标悬停时添加下划线 */
}

.news-content {
    color: #333;
    white-space: nowrap;
}

/* 针对特定 news-carousel 组件的箭头样式 */
.news-carousel .el-carousel__arrow--left {
    left: 100px;
    /* 固定左箭头的位置 */
}

.news-carousel .el-carousel__arrow--right {
    right: 100px;
    /* 固定右箭头的位置 */
}
</style>